import React, { useState } from 'react';


const AddComment = ({ articleName, setArticleInfo })=>{
    const [ username, setUsername ] = useState('');
    const [ commentText, setCommentText ] = useState('');
    const addCommentFunc = async ()=>{
        const result = await fetch(`/api/article/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({ username, comment: commentText}),
            headers: {
                'Content-Type': 'application/json',
            }
        })
        const body = await result.json();
        // console.log(body);
        setArticleInfo(body);
        setUsername('');
        setCommentText('');
    }
    return (
        <div className="add-comment-box">
            <h3>Add a comment</h3>
            <label>
                Username
                <br/>
                <input type="text" value = { username } onChange = {(event)=> setUsername(event.target.value)}/>
                <br/>
            </label>
            <label>
                Comment
                <br/>
                <textarea rows = "4" cols = "50" value={ commentText } onChange = {(event)=> setCommentText(event.target.value)}/>
                <br/>
            </label>
            <button onClick = { ()=> addCommentFunc() }>Add comment</button>
        </div>
    );
}

export default AddComment;