import React, { useState, useEffect } from 'react';
import articleContent from './articles-content';
import ArticleList from '../components/ArticleList';
import NotPageFound from './NotPageFound';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddComment from '../components/AddComment';


const ArticlePage = ({ match })=>{
    const name = match.params.name;
    const article = articleContent.find(article=>article.name === name);
    const otherArticles = articleContent.filter(article=>article.name!== name);

    const [ articleInfo, setArticleInfo ] = useState({ upvote: 0, comment: []});

    useEffect(()=>{
        const fetchData = async ()=>{
            const result = await fetch(`/api/article/${name}`);
            const body = await result.json();            
            setArticleInfo(body);
        }
        fetchData();
        
    }, [name])
    
    if(!article){
        return (            
                <NotPageFound/>      
        );
    }

    
    return (
        <>
            <h1>
                {article.title}
            </h1>
            
            {
                article.content.map((paragraph, key)=>(
                <p key = {key}>{paragraph}</p>
                ))
            }
            <UpvotesSection articleName = { name } upvote = { articleInfo.upvote } setArticleInfo = { setArticleInfo } />

            <CommentsList comments = { articleInfo.comment } />
            <AddComment articleName = { name } setArticleInfo = { setArticleInfo }/>

            <h3>Other articles:</h3>
            <ArticleList articleContent = { otherArticles }/>
           
        
        </>
    );

}

export default ArticlePage;