import React from 'react';

const UpvotesSection = ({ articleName, upvote, setArticleInfo })=>{
    const upvoteClick = async ()=>{
        const resultUV = await fetch(`/api/article/${articleName}/upvote`, {
            method: 'POST',
        });
        const body = await resultUV.json();
        setArticleInfo(body);

    }

    const downvoteClick = async () =>{
        
            const resultDV = await fetch(`/api/article/${articleName}/downvote`, {
                method: 'POST',
            })
            const body = await resultDV.json();
            setArticleInfo(body);
    }
    return (
        <div className="upvote-section">
            <button className="btn-upvote" onClick = {()=>upvoteClick()} >Upvote</button>
            <button className = "btn-downvote" onClick = { ()=> downvoteClick() }>Downvote</button>
            <p> {upvote} </p>
        </div>

    );
}

export default UpvotesSection;